.loader {
  width: 100px;
  height: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
  overflow: hidden;

  &-line {
    width: 0;
    height: 100%;
    background-color: #007bff;

    animation: loaderAnimation 2s linear infinite;
  }
}

@keyframes loaderAnimation {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
